export default {
		data() {
			return {
				userName:"",
				orderNum:"",
				handled:false,
				messageId:''
			};
		},
	
		onLoad(params) {
			console.log(params);
			var me = this;
			var globalUser = this.getGlobalUser("userInfos");
			var messageId = params.messageId;
			me.messageId=messageId;
			uni.request({
				url:this.mainUrl+"user/message/messageInfo",
				data:{
					message_id:messageId,
				},
				method:"GET",
				success: (res) => {
					if(res.data.status=="success"){
						console.log(res);
						me.userName=res.data.data.userName;
						me.orderNum=res.data.data.orderNum;
						
						if(res.data.data.status==2){
							this.handled=true;
						}
					}else if(res.data.status=="fail"){
						console.log(res.errmsg);
					}
				}
			});
		},
		
		methods: {
			handle:function(judge){
				var globalUser = this.getGlobalUser("userInfos");
				console.log(this.messageId);
				uni.request({
					url:this.mainUrl+"user/message/handleMessage/"+judge,
					data:{
						message_id:this.messageId,
						user_id:globalUser.userId
					},
					method:"GET",
					success: (res) => {
						if(res.data.status=="success"){
							console.log(res);
							this.handled=true;
						}
					}
				});
			},
			
			agree:function(){
				this.handle('accept');
			},
			disagree:function(){
				this.handle('refuse');
			}
		},
		
		components: 
		{
			
		}
	}
	
